'use client'

import { useState, useEffect } from 'react'
import type { 
  Calendar, 
  WorkingHours, 
  DateOverride, 
  TimeBlock,
  CalendarMember,
  AppointmentType
} from '@/lib/calendar/types'

// ==========================================
// TYPES
// ==========================================

interface CalendarSettings {
  calendar: Partial<Calendar>
  bookingSettings: {
    durationMinutes: number
    slotIntervalMinutes: number
    bufferBeforeMinutes: number
    bufferAfterMinutes: number
    minimumNoticeHours: number
    maximumBookingDays: number
    autoConfirm: boolean
    requiresApproval: boolean
    maxCapacity?: number
    minAttendees?: number
    waitlistEnabled: boolean
    allowGuests: boolean
    maxGuests: number
  }
  reschedulingSettings: {
    allowReschedule: boolean
    rescheduleDeadlineHours?: number
    maxReschedules?: number
    allowCancellation: boolean
    cancellationDeadlineHours?: number
  }
  workingHours: WorkingHours[]
  dateOverrides: DateOverride[]
}

interface Props {
  calendarId: string
  onClose: () => void
  onUpdated: () => void
}

// ==========================================
// MAIN COMPONENT
// ==========================================

export default function CalendarSettingsModal({ calendarId, onClose, onUpdated }: Props) {
  const [activeTab, setActiveTab] = useState<'general' | 'availability' | 'booking' | 'team' | 'types'>('general')
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [settings, setSettings] = useState<CalendarSettings | null>(null)
  const [members, setMembers] = useState<CalendarMember[]>([])
  const [appointmentTypes, setAppointmentTypes] = useState<AppointmentType[]>([])
  const [error, setError] = useState<string | null>(null)

  // Load settings on mount
  useEffect(() => {
    loadSettings()
    loadMembers()
    loadAppointmentTypes()
  }, [calendarId])

  const loadSettings = async () => {
    try {
      const res = await fetch(`/api/v1/calendars/${calendarId}/settings`)
      const data = await res.json()
      if (data.success) {
        setSettings(data.data)
      } else {
        setError(data.error)
      }
    } catch (err) {
      setError('Failed to load settings')
    } finally {
      setLoading(false)
    }
  }

  const loadMembers = async () => {
    try {
      const res = await fetch(`/api/v1/calendars/${calendarId}/members`)
      const data = await res.json()
      if (data.success) {
        setMembers(data.data.members || [])
      }
    } catch (err) {
      console.error('Failed to load members:', err)
    }
  }

  const loadAppointmentTypes = async () => {
    try {
      const res = await fetch(`/api/v1/calendars/${calendarId}/appointment-types?activeOnly=false`)
      const data = await res.json()
      if (data.success) {
        setAppointmentTypes(data.data || [])
      }
    } catch (err) {
      console.error('Failed to load appointment types:', err)
    }
  }

  const saveSettings = async () => {
    if (!settings) return
    setSaving(true)
    setError(null)

    try {
      const res = await fetch(`/api/v1/calendars/${calendarId}/settings`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...settings.calendar,
          ...settings.bookingSettings,
          ...settings.reschedulingSettings,
          workingHours: settings.workingHours,
          dateOverrides: settings.dateOverrides
        })
      })

      const data = await res.json()
      if (data.success) {
        onUpdated()
        // Show success message
        alert('Settings saved successfully!')
      } else {
        setError(data.error || 'Failed to save settings')
      }
    } catch (err) {
      setError('Failed to save settings')
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
        <div className="bg-white rounded-xl p-8">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading settings...</p>
        </div>
      </div>
    )
  }

  if (!settings) {
    return (
      <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
        <div className="bg-white rounded-xl p-8 max-w-md">
          <p className="text-red-600">{error || 'Failed to load settings'}</p>
          <button onClick={onClose} className="mt-4 px-4 py-2 bg-gray-100 rounded-lg">Close</button>
        </div>
      </div>
    )
  }

  const tabs = [
    { id: 'general', label: 'General', icon: 'cog' },
    { id: 'availability', label: 'Availability', icon: 'clock' },
    { id: 'booking', label: 'Booking Rules', icon: 'calendar-check' },
    { id: 'team', label: 'Team Members', icon: 'users' },
    { id: 'types', label: 'Appointment Types', icon: 'list' }
  ] as const

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4">
      <div className="bg-white rounded-xl shadow-2xl w-full max-w-4xl max-h-[90vh] flex flex-col">
        {/* Header */}
        <div className="flex items-center justify-between px-6 py-4 border-b">
          <div>
            <h2 className="text-xl font-bold text-gray-900">Calendar Settings</h2>
            <p className="text-sm text-gray-500">{settings.calendar.name}</p>
          </div>
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
            <i className="fas fa-times text-xl"></i>
          </button>
        </div>

        {/* Tabs */}
        <div className="flex border-b px-6">
          {tabs.map(tab => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`px-4 py-3 text-sm font-medium border-b-2 -mb-px ${
                activeTab === tab.id
                  ? 'border-blue-600 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              <i className={`fas fa-${tab.icon} mr-2`}></i>
              {tab.label}
            </button>
          ))}
        </div>

        {/* Content */}
        <div className="flex-1 overflow-y-auto p-6">
          {error && (
            <div className="mb-4 p-4 bg-red-50 text-red-700 rounded-lg">
              {error}
            </div>
          )}

          {activeTab === 'general' && (
            <GeneralSettingsTab 
              settings={settings} 
              onUpdate={(updates) => setSettings({ ...settings, calendar: { ...settings.calendar, ...updates } })}
            />
          )}

          {activeTab === 'availability' && (
            <AvailabilityTab 
              workingHours={settings.workingHours}
              dateOverrides={settings.dateOverrides}
              onUpdateWorkingHours={(wh) => setSettings({ ...settings, workingHours: wh })}
              onUpdateDateOverrides={(overrides) => setSettings({ ...settings, dateOverrides: overrides })}
            />
          )}

          {activeTab === 'booking' && (
            <BookingRulesTab 
              bookingSettings={settings.bookingSettings}
              reschedulingSettings={settings.reschedulingSettings}
              onUpdateBooking={(updates) => setSettings({ 
                ...settings, 
                bookingSettings: { ...settings.bookingSettings, ...updates }
              })}
              onUpdateRescheduling={(updates) => setSettings({ 
                ...settings, 
                reschedulingSettings: { ...settings.reschedulingSettings, ...updates }
              })}
            />
          )}

          {activeTab === 'team' && (
            <TeamMembersTab 
              calendarId={calendarId}
              calendarType={settings.calendar.calendarType || 'individual'}
              members={members}
              onRefresh={loadMembers}
            />
          )}

          {activeTab === 'types' && (
            <AppointmentTypesTab 
              calendarId={calendarId}
              types={appointmentTypes}
              onRefresh={loadAppointmentTypes}
            />
          )}
        </div>

        {/* Footer */}
        <div className="flex items-center justify-between px-6 py-4 border-t bg-gray-50">
          <button
            onClick={onClose}
            className="px-4 py-2 text-gray-600 hover:text-gray-800"
          >
            Cancel
          </button>
          <button
            onClick={saveSettings}
            disabled={saving}
            className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
          >
            {saving ? 'Saving...' : 'Save Changes'}
          </button>
        </div>
      </div>
    </div>
  )
}

// ==========================================
// GENERAL SETTINGS TAB
// ==========================================

